import React from 'react';
import Slider from 'react-slick';
import './SliderComponent.css';
import img1 from '../../images/lemons.png';
import img2 from '../../images/kiwi.png';
import img3 from '../../images/orange.png';

export const SliderComponent = () => {
  const slides = [
    {
      image: img1,
      title: '    ',
      description:
        "      !   '   -   .          !",
      buttonText: '  ',
      price: '50./',
    },
    {
      image: img2,
      title: '     !',
      description:
        "         !                .     '   ",
      buttonText: '  ',
      price: '180./',
    },
    {
      image: img3,
      title: '    !',
      description:
        '       C   !       -  .        ,    !',
      buttonText: '  ',
      price: '150./',
    },
  ];

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,
    autoplaySpeed: 3000,
  };

  return (
    <div className="slider-container">
      <Slider {...settings}>
        {slides.map((slide, index) => (
          <div key={index} className={`slide bg-${index + 1}`}>
            <div className="slide-content">
              <div className="slide-inner text-content">
                <h2 className="slide-title">{slide.title}</h2>
                <p className="slide-text">{slide.description}</p>
                <button className={`slide-button bt-${index + 1}`}>
                  {slide.buttonText}
                </button>
              </div>
              <div className="slide-inner wrap-slide-img">
                <img
                  src={slide.image}
                  alt={`Slide ${index + 1}`}
                  className="slide-img"
                />
              </div>
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default SliderComponent;
